body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: grid;
  min-height: 100vh;
  grid-template-rows: auto 1fr auto;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.form-login{
  width: 500px;
  text-align: center;
   margin: auto;
}
#myVideo {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  z-index: -1;
}

.home-content{
  color:#CCC;
  margin: 30px;
}
.spacing {
  margin-top: 80px;
}

.carousel-image-jesters {
  width: 100%;
  height: 400px;
  margin: auto;
}

.carousel-image-jesters img {
  width: 100%;
}

.carousel-photo-jesters {
  width: 100%;
  height: 500px;
  margin: auto;
}

.carousel-photo-jesters img {
  width: 100%;
}

.jesters-title {
  font-size: 1.75rem !important;
}

.app-footer {
  width: 100%;
  height: 250px;
  background-color: rgb(25, 25, 27);
  color: #888;
}
.app-footer a {
  text-decoration: none !important;
  color: #FFF;
}

.App {
  text-align: center;
  min-height: calc(100vh - 330px - 110px);
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.containerPrincipal{
  width: 350px;
  text-align: center;
  position: relative;
  margin: auto;
}
.js-calendar {
  text-align: center;
  color: white;
  background-color: #000000;
} 

.position {
  width: 1375px;
  position: relative;
  margin: auto;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: rgb(255, 255, 255);
  height: 150px;
  padding: 20px;
